#graphmilker .gm-button {
  background: rgba(255,112,67,1);
  border: none;
  color: #fff;
  font-size: 12px;
  line-height: 20px;
  padding: 5px 10px;
  cursor: pointer;
}

#graphmilker .gm-button:focus,
#graphmilker .gm-button:hover {
  outline: none;
  background: rgba(198,63,23,1);
}

#graphmilker .gm-button--secondary {
  background: #afb0af;
}
#graphmilker .gm-button--secondary:focus,
#graphmilker .gm-button--secondary:hover {
  background: rgba(198,63,23,1);
}


